export const reducer = (state, action) => {
	switch (action.type) {
		case 'SET_USER_DATA':
			return {
				...state,
				userInfo: action.payload,
			};
		case 'SET_PREVIOUS_ROUTE':
			const test = {
				...state,
				isTestStarted: action.payload,
			};
			return test;
		default:
			throw new Error();
	}
};
