import React, { Fragment, useCallback, useEffect, useReducer } from 'react';
import { BrowserRouter as Router, Switch } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';
import './App.scss';
import Toaster from './common/component/Toaster/Toaster';
import { initialState } from './hooks/initialState';
import { reducer } from './hooks/reducer';
import { MyContext } from './hooks/UserContext';
import Routes from './routes/index';
import ScrollToTop from './ScrollToTop';
const App = () => {
	const [state, dispatch] = useReducer(reducer, initialState);
	const value = { state, dispatch };
	const preventEvent = useCallback((event, isAddEvent) => {
		if (isAddEvent) {
			event.preventDefault();
		}
	}, []);
	useEffect(() => {
		const userSelection = document.getElementsByClassName('question-answer');
		if (_.size(userSelection)) {
			for (let i = 0; i < userSelection.length; i++) {
				((index) => {
					userSelection[index].addEventListener('contextmenu', (event) =>
						preventEvent(event, true)
					);
					userSelection[index].addEventListener('cut', (event) =>
						preventEvent(event, true)
					);
					userSelection[index].addEventListener('copy', (event) =>
						preventEvent(event, true)
					);
					userSelection[index].addEventListener('paste', (event) =>
						preventEvent(event, true)
					);
				})(i);
			}
		}
	});
	return (
		<MyContext.Provider value={value} displayName="React Context">
			<Router>
				<Fragment>
					<Toaster />
					<ScrollToTop />
					<Switch>
						<Routes />
					</Switch>
				</Fragment>
			</Router>
		</MyContext.Provider>
	);
};
export default App;
