import React, { useState, useRef, useEffect } from 'react';
import * as api from '../../../apiHelper/Apihelper';
import { useLocation, useHistory } from 'react-router-dom';
import './userTestSeries.scss';
import _ from 'lodash';
import {
	toastEmitterError,
	toastEmitterWarning,
} from '../../../../../../common/component/Toaster/ToastEmitter';
import { Link } from 'react-router-dom';

const userTestSeries = (props) => {
	const history = useHistory();
	const { state } = useLocation();
	const [loader, setLoader] = useState(false);
	const [errMsg, setErrMsg] = useState('');

	const [gettoken, setGettoken] = useState(localStorage.getItem('token'));
	const [testSeriesList, setTestSeriesList] = useState([]);

	useEffect(() => {
		getTestSeries();
	}, []);

	async function getTestSeries() {
		const streamInput = {
			user_id: state.userInfo.user_id,
		};
		fetch('http://54.251.156.235:4001/commons/user-active-test-series', {
			method: 'POST',
			headers: {
				Accept: 'application/json',
				'Content-Type': 'application/json',
				Authorization: localStorage.getItem('token'),
			},
			body: JSON.stringify(streamInput),
		})
			.then((response) => response.json())
			.then((responseData) => {
				if (responseData.statusCode === 200) {
					setTestSeriesList(responseData.data);
				} else {
					toastEmitterError(responseData.message);
				}
			});
	}
	return (
		<div className="row_full user-test">
			<h3 className="mar_b_10 text-center">Test Series</h3>
			{testSeriesList.map((series) => (
				<div className="series">
					<h5 className="mb-2">{series.test_type_name}</h5>
					<div className="d-flex flex-row">
						{series.test_series_data.map(
							({ test_count, test_series_name, test_series_id }) => (
								<div className="card">
									<div className="card-body" style={{ padding: '1rem' }}>
										<div className="d-flex justify-content-end">
											<span className="badge badge-info">{test_count}</span>
										</div>
										<h6 className="h6 f-s-16 f-w-600 lt-1 my-2 text-start">
											{test_series_name}
										</h6>
										<div className="divider"></div>
										<Link
											className="btn btn-sm mt-2 btn-border-info w-100"
											to={{
												pathname: '/global/testlist',
												state: {
													userInfo: state.userInfo,
													test_series_id: test_series_id,
												},
											}}
										>
											View Test
										</Link>
									</div>
								</div>
							)
						)}
					</div>
				</div>
			))}
		</div>
	);
};

export default userTestSeries;
