import React, { useState, useRef, useEffect } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import './testList.scss';
import _ from 'lodash';
import { Link } from 'react-router-dom';
import { toastEmitterError } from '../../../../../../../common/component/Toaster/ToastEmitter';

const TestList = (props) => {
	const history = useHistory();
	const { state } = useLocation();
	const [loader, setLoader] = useState(false);
	const [errMsg, setErrMsg] = useState('');

	const [gettoken, setGettoken] = useState(localStorage.getItem('token'));
	const [testList, setTestList] = useState([]);

	useEffect(() => {
		getTestList();
	}, []);

	async function getTestList() {
		const streamInput = {
			user_id: state.userInfo.user_id,
			test_series_id: state.test_series_id,
		};
		fetch('http://54.251.156.235:4001/commons/user-active-test', {
			method: 'POST',
			headers: {
				Accept: 'application/json',
				'Content-Type': 'application/json',
				Authorization: localStorage.getItem('token'),
			},
			body: JSON.stringify(streamInput),
		})
			.then((response) => response.json())
			.then((responseData) => {
				if (responseData.statusCode === 200) {
					setTestList(responseData.data.attempted_test);
				} else {
					toastEmitterError(responseData.message);
				}
			});
	}
	return (
		<div className="row_full user-test">
			<h4 className="mar_b_10 text-center">Test List</h4>
			{_.size(testList) > 0 &&
				testList.map(
					({
						test_id,
						test_name,
						test_total_question,
						test_total_marks,
						test_duration,
						test_passing_marks,
						test_negative_marking,
					}) => (
						<div className="test-list w-100">
							<div className="card w-100">
								<div className="card-body d-flex">
									<div className="flex-grow-1">
										<div className="h5 f-s-16 f-w-600 text-start">
											{test_name}
										</div>
										<div className="details f-s-14">
											<span className="pr-3">
												Total Question: {test_total_question}
											</span>
											<span className="pr-3">
												Total Marks: {test_total_marks}
											</span>
											<span className="pr-3">
												Duration: {test_duration} minute
											</span>
											<span className="pr-3">
												Passing Marks: {test_passing_marks}
											</span>
											<span className="pr-3">
												Negative Marking: {test_negative_marking ? 'Yes' : 'No'}
											</span>
										</div>
									</div>
									<div className="">
										<Link
											className={`d-flex justify-content-center btn-sm btn-border-info`}
											to={{
												pathname: '/global/test-response',
												state: {
													userInfo: state.userInfo,
													test_id: test_id,
												},
											}}
										>
											Response
										</Link>
									</div>
								</div>
							</div>
						</div>
					)
				)}
			{!_.size(testList) ? <p>No active test found!</p> : null}
		</div>
	);
};

export default TestList;
