import React, { useState, useRef, useEffect } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import './TestResponse.scss';
import _ from 'lodash';
import { toastEmitterError } from '../../../../../../../common/component/Toaster/ToastEmitter';
const TestResponse = (props) => {
	const history = useHistory();
	const { state } = useLocation();
	const [loader, setLoader] = useState(false);
	const [errMsg, setErrMsg] = useState('');

	const [gettoken, setGettoken] = useState(localStorage.getItem('token'));
	const [testList, setTestList] = useState([]);
	const [testResponse, setTestResponse] = useState([]);
	const [answerDetails, setAnswerDetails] = useState([]);

	useEffect(() => {
		getTestList();
	}, []);
	console.log(state.userInfo);
	async function getTestList() {
		const streamInput = {
			test_id: state.test_id,
			user_id: state.userInfo.user_id,
		};
		console.log(streamInput);
		fetch('http://54.251.156.235:4001/commons/user-test-response', {
			method: 'POST',
			headers: {
				Accept: 'application/json',
				'Content-Type': 'application/json',
				Authorization: localStorage.getItem('token'),
			},
			body: JSON.stringify(streamInput),
		})
			.then((response) => response.json())
			.then((responseData) => {
				if (responseData.statusCode === 200) {
					setTestResponse(responseData.data);
					setAnswerDetails(responseData.otherdata);
				} else {
					toastEmitterError(responseData.message);
				}
			});
	}
	return (
		<div className="row_full test-response">
			<h4 className="mar_b_10 text-center">Test Response</h4>
			<div className="response-header">
				<div className="user-info">
					<div>
						<strong className="pl-2">Name:</strong> {state.userInfo.full_name}
					</div>
					<div>
						<strong className="pl-2">Email:</strong> {state.userInfo.email}
					</div>
					<div>
						<strong className="pl-2">Mobile:</strong> {state.userInfo.mobile}
					</div>
				</div>
				{testResponse.length > 0 ? (
					<div className="answer-details">
						<div>
							<strong className="pl-2">Total Marks:</strong>{' '}
							{answerDetails.total_marks}
						</div>
						<div>
							<strong className="pl-2">Attempt:</strong>{' '}
							{answerDetails.attempt_ques}
						</div>
						<div>
							<strong className="pl-2">Not Attempt:</strong>{' '}
							{answerDetails.non_attempt_ques}
						</div>
						<div>
							<strong className="pl-2">Total Correct:</strong>{' '}
							{answerDetails.test_correct_ans}
						</div>
						<div>
							<strong className="pl-2">Total Not Correct:</strong>{' '}
							{answerDetails.test_wrong_ans}{' '}
						</div>
					</div>
				) : null}
			</div>
			{testResponse.length === 0 && <p className="m-2">No test found!</p>}
			{testResponse.map((question) => (
				<div className="question-wrapper">
					<div
						className={`question-content p-3 ${
							question.is_attempt
								? question.is_correct
									? 'correct-answer'
									: 'not-correct-answer'
								: ''
						}`}
					>
						{question.is_attempt ? (
							<div className="correct-question-icon">
								{question.is_correct ? (
									<i class="fa fa-check-circle" aria-hidden="true"></i>
								) : (
									<i class="fa fa-times-circle" aria-hidden="true"></i>
								)}
							</div>
						) : null}

						<div className="question-description">
							<span className="pr-2">
								<strong>{question.serial_no}).</strong>
							</span>
							<strong>
								<span
									dangerouslySetInnerHTML={{
										__html: question.mongoData.problem_description,
									}}
								/>
							</strong>
						</div>
						<div className="question-option">
							{question.problem_actual_type_id !== 9 && (
								<div className="divider mb-2"></div>
							)}
							{/* {question.problem_actual_type_id === 9 && (
								<div className="text-area">
									<textarea
										style={{ minHeight: '50px', lineHeight: '20px' }}
										className="form-control"
										placeholder="Answer"
										name="option-description"
										// value={selectedOption.ques_response}
									></textarea>
								</div>
							)} */}
							<ul>
								{JSON.parse(question.mongoData.multiple_options).map(
									(option) => (
										<li className="checkbox">
											{question.problem_actual_type_id === 10 && (
												<label className="d-flex">
													{`${option.index + 1}. `}
													<span
														className="ml-1"
														dangerouslySetInnerHTML={{
															__html: option.text,
														}}
													/>
												</label>
											)}
											{(question.problem_actual_type_id === 18 ||
												question.problem_actual_type_id === 8) && (
												<label className="d-flex">
													{`${option.index + 1}.`}
													<span
														className="ml-1"
														dangerouslySetInnerHTML={{
															__html: option.text,
														}}
													/>
												</label>
											)}
										</li>
									)
								)}
							</ul>
						</div>
						<div className="divider mb-2"></div>
						<div className="answer-wrapper">
							<strong>Your Answer :</strong>
							{question.is_attempt ? (
								<span> {question.user_response}</span>
							) : (
								<span> Not Attempt</span>
							)}
							<span className="ml-2">
								<strong>Correct Answer :</strong>
								<span> {question.mongoData.correct_options}</span>
							</span>
						</div>
						<div>
							<strong>Detailed solutions :</strong>
							<span
								className="ml-1"
								dangerouslySetInnerHTML={{
									__html: JSON.parse(question.mongoData.detailed_solutions),
								}}
							/>
						</div>
					</div>
				</div>
			))}
		</div>
	);
};

export default TestResponse;
