import React, { memo, useContext, useEffect, useState } from 'react';
import {
	Router,
	Route,
	Switch,
	Redirect,
	useHistory,
	useLocation,
} from 'react-router-dom';
import history from '../utils/history';
import PrivateRoutes from './PrivateRoutes';
import Auth from './Auth';
import Header_new from '../containers/views/Client/header_component_new/Header_new';
import NotFound from '../common/component/NotFound/NotFound';
import Superadmin from '../containers/views/Client/superadmin_component/Superadmin';
import { MyContext } from '../hooks/UserContext';
import {
	toastEmitterError,
	toastEmitterSuccess,
} from '../common/component/Toaster/ToastEmitter';

const Routes = () => {
	const history = useHistory();
	let location = useLocation();
	const { state, dispatch } = useContext(MyContext);
	useEffect(() => {
		if (state.isTestStarted && location.pathname.search('/exam') === -1) {
			fetch('http://54.251.156.235:4004/api/commons/pause-test', {
				method: 'GET',
				headers: {
					Accept: 'application/json',
					'Content-Type': 'application/json',
					Authorization: localStorage.getItem('token'),
				},
			})
				.then((response) => response.json())
				.then((responseData) => {
					if (responseData.statusCode === 200) {
						toastEmitterSuccess(responseData.message);
						dispatch({
							type: 'SET_PREVIOUS_ROUTE',
							payload: false,
						});
					}
				})
				.catch((error) => {
					toastEmitterError(responseData.message);
				});
		}
		if (!state.isTestStarted && location.pathname.search('/exam') > -1) {
			dispatch({
				type: 'SET_PREVIOUS_ROUTE',
				payload: true,
			});
		}
	});

	return (
		<Router history={history}>
			<Switch>
				<Route path="/studentegyan">
					<PrivateRoutes path={'studentegyan'} />
				</Route>
				<Route path="/studentpathshala">
					<PrivateRoutes path={'studentpathshala'} />
				</Route>
				<Route path="/pathshala">
					<PrivateRoutes path={'pathshala'} />
				</Route>
				<Route path="/global">
					<PrivateRoutes path={'global'} />
				</Route>
				<Route path="/">
					<Auth />
				</Route>
				{/* <Route path="/*">
					<Redirect to="/" />;
				</Route> */}
			</Switch>
		</Router>
	);
};

export default memo(Routes);
